import React, {useRef, useEffect, useState} from 'react';
import gsap from 'gsap';

import logo from './logo.svg';
import './App.css';
import Nav from './templates/nav.js'
import MainContainer from "./templates/main";
import Animation from "./templates/animation";
import arrow from "./arrows-rotate.svg";
import { DrawSVGPlugin } from "gsap/DrawSVGPlugin";
import { GSDevTools } from "gsap/GSDevTools";
import { SplitText } from "gsap/SplitText";
gsap.registerPlugin(DrawSVGPlugin, GSDevTools, SplitText);
function App() {
  // CHANCE BAR
  const [bar, setBar] = useState(0);
  // RESULTS
  const [arr, setArr] = useState([]);
  const newArr = [];
  
  // RANDOM NUMBER
  const [endX, setEndX] = useState(0);
    const boxRef = useRef();
    const randomX = gsap.utils.random(0, 100, 1, true);
    useEffect(() => {
        gsap.to(boxRef.current, {
        //   x: endX
        });
      }, [endX]);
  
  // COLORS
  const fail = "#901717";
  const success = "#34AB55"
  const listItems = arr.map((number, index) => <li className="list-inline-item mb-2" style={{background: `${number > bar ? fail : success}`}}>{number}</li>);
  // INFO
  const successText = "Udao si ! Trafie ";
  const failText = "Ooops...nie udao si "
  // RESET ARRAY
  const Reset = () => {
    setArr([]);
    alert("Zresetowano pomylnie")
  }
  return (
    <div className="App">
      <header>
        <div class="text-center mt-2">
            <h4> Random Number Generator</h4>
        </div>
      </header>
      <main>
        <div className="container px-3">
          <div className="row row-cols-1">
              <div className="col">
                  <div className="place-center mt-3">
                      
                      <div className="text-center h-200 place-center" style={{borderRadius: "200px", border: `2px solid ${endX > bar ? fail : success}`}}>
                          <h1 style={{fontSize: "100px", fontFamily: "Poppins, sans-serif", color: `${endX > bar ? fail : success}`}}>{endX}</h1>
                      </div>
                  </div>
              </div>
              <div className="col">
                  <div className="place-center mt-3" style={{fontFamily: "Poppins, sans-serif"}}>
                      <h2 className="mb-4" style={{fontFamily: "Poppins, sans-serif"}}>{endX > bar ? failText : successText}</h2>
                      <div className="line-bg">
                          <div className="line-bg" style={{background: success, width: `${bar}%`}}></div>
                      </div>
                  </div>
              </div>
              <div className="col">
                  <div className="w-100 mt-3" style={{height: "50px", background: "#F9F9F9"}}></div>
              </div>
          </div>
        </div>
        <div className="container mt-4">
          <div className="col">
              <p className="mt-2" style={{color: "#7E7C7C"}}>Ostatnie {arr.length} losowa</p>
          </div>
          <div className="row">
              <div className="col">
                  <div>
                      <ul className="list-inline">
                          {listItems.reverse()}
                      </ul>
                  </div>
              </div>
          </div>
        </div>
        
      </main>
      <footer>
      <div className="container">
          <div>
          <p>Podaj procent szansy na sukces i kliknij generuj liczb</p>
          <div className="grid-custom">
              <div className="col">
                <input className="p-2 inputNumber" type="number" onChange={(e) => setBar(e.target.value)} min="1" max="100" />
              </div>
              <div className="col">
                <button className="text-nowrap btn-custom" type="button" style={{background: "#2C3E50"}} onClick={() => setEndX(randomX())} onMouseUp={(e) => setArr(arr => [...arr, endX  ])}>generuj liczb</button>
              </div>
              <div className="col">
                <button className="btn-custom" type="button" style={{background: "#2C3E50"}} onClick={() => Reset()}>
                  <img src={arrow} alt="Reset" />
                </button>
              </div>
          </div>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default App;
